import React from 'react';


const WebAbout = (props) => {
    return (
        <p style={{ maxWidth: 750, margin: 'auto', textAlign: 'left' }}>
            <span id="third-container-header">Behind the Name</span>
            <img src={props.imageSrc} id="third-container-image" alt="Wood Design" />
            {props.behindTheName[0]}
            {props.behindTheName[1]}<br />
            {props.behindTheName[2]}
        </p>
    )
}

export default WebAbout;